@import 'Mixin';

.Folder{
    position:absolute; width:100%; height:100%; background:#F1E8D9;

    .topBar{
        width:100%; height:52px; background:#DCBD9E;
        @include flex(flex-end,center);

        .closeButton{
            margin-right:30px; @include whb(30px,30px,url('./images/close_btn.png'));
            background-size:cover; cursor:pointer;
        }
    }
    ul{
        @include whb(95%,95%); @include flex(none, flex-start);
        flex-direction: column;

        li{
            @include whb(100px,100px,none); cursor:pointer; @include flex(center,center);
            flex-direction: column;
            .folder{
                @include whb(65px,65px,url("./images/Folder.png"));
            }

            .memo{
                @include whb(50px,65px,url("./images/Memo.png"));
            }
        }
    }
}