@import 'Mixin';

.Modal{
    position:absolute; z-index:20; top:0; left:0; @include whb(100vw,100vh,rgba(80, 80, 80, 0.7)); @include flex(center,center);

    .modal_wrapper{
        @include whb(700px,350px,#F1E8D9); border-radius:30px; @include flex(center,center);

        .content{
            @include whb(550px,220px,none); @include flex(space-around,); flex-direction:column;

            p{
                font-size:30px; text-align:center; 
            }
            a{
                @include whb(100%,50px,#F97173); border-radius:15px;
                cursor:pointer; font-size:20px; display:block; text-align:center;
                color:#000; line-height:50px;
            }
            a:hover{
                background:#F97173bf;
            }
            button{
                @include whb(100%,50px,#F97173); border-radius:15px;
                cursor:pointer; font-size:20px;
            }
            button:hover{
                background:#F97173bf;
            }
        }
    }
}