@import 'Mixin';

.main{
    @include whb(100%,100%,#EFCFBA); position:relative;

    
    .main_wrapper{
        @include whb(100%,100%); @include flex(center,);
        flex-direction: column;

        &>div{
            box-sizing:border-box;
        }
        .user_box{
            @include whb(100%,5%); 

            .user_wrap{
                float:right; @include flex(none,center);

                .logout{
                    @include whb(90px,28px); border:1px solid #F97173; color:#F97173;
                    border-radius:10px; text-align:center; margin-right:20px;
                    cursor:pointer; margin-top:2px;
                }
                .logout:hover{
                    background:#ffffff9e;
                }
            .user_icon{
                @include whb(35px,35px,url('./images/user.png')no-repeat center center);
                margin-right:5px;
            }
            .user_name{
                margin-right:50px;
            }
            }
            
        }

        .memo_box{
            @include whb(100%,85%); @include flex(center, center);
            
        .Folder{
            @include whb(100%,100%,#F1E8D9); position:absolute;
        }

            ul{
                @include whb(95%,95%); @include flex(none);
                flex-direction: column; flex-wrap: wrap; overflow-y: auto;

                li{
                    @include whb(100px,100px,none); cursor:pointer; @include flex(center,center);
                    flex-direction: column;
                    .folder{
                        @include whb(65px,65px,url("./images/Folder.png"));
                    }

                    .memo{
                        @include whb(50px,65px,url("./images/Memo.png"));
                    }
                    p{
                        width:80%; text-align:center; overflow: hidden;text-overflow: ellipsis; white-space: nowrap;
                    }
                    
                    
                }
            }
        }

        .create_box{
            @include whb(100%,10%,rgba(0, 0, 0, 0.79)); @include flex(center, center); gap:50px;
            
            .addMemo{ 
                display:inline-block; @include whb(40px,50px,url("./images/Memo.png")no-repeat center center);
                position:relative; background-size:cover;
            }
            .addMemo::after {
                position:absolute;
                content: "+";
                font-size: 45px;
                bottom:-15px;
                right:0;
                color:#1A5194;
            }
            .addFolder{
                @include whb(65px,65px,url("./images/Folder.png")); cursor:pointer;
                position:relative;
            }
            .addFolder::after {
                position:absolute;
                content: "+";
                font-size: 50px;
                bottom:-10px;
                right:0;
                color:#1A5194;
            }
        }

        ul{
            @include whb(100%,100%);
        }
    }
}