@import 'Mixin';

.initcontents{
    @include whb(100%,100%);

    .initcontents_wrapper{
        @include whb(100%,100%); @include flex(center,center);
    
        form{
            @include whb(80%,100%,); @include flex(space-evenly,center); flex-direction:column;

            .title{
                @include whb(100%,35px,#fff); border:1px solid #959595; border-radius:6px;
                padding-left:10px; box-sizing:border-box; margin-top:20px;
            }
            .content{
                @include whb(100%,450px,#fff); border:1px solid #959595; border-radius:6px;
                padding:10px; box-sizing:border-box; resize: none;
            }

            .btn_wrap{
                @include whb(200px,50px); @include flex(space-between,center);
            }
            a{
                display:block; @include whb(88px,26px); border:1px solid #F97173; color:#F97173;
                    border-radius:10px; text-align:center; cursor:pointer; font-size:16px; font-weight:normal
            }
            a:hover{
                background:#f0f8ffbd;
            }
            .save{
                display:block; @include whb(90px,28px); border:1px solid #F97173; color:#F97173;
                    border-radius:10px; text-align:center; cursor:pointer; font-size:16px; font-weight:normal
            }
            .save:hover{
                background:#f0f8ffbd;
            }
        }
        
    }
}