@import 'Mixin';

.login{
    @include whb(100%,100%,#EFCFBA); @include flex(center,center);

    .login_wrapper{
         border-radius:30px;

        form{
            @include whb(100%,100%); @include flex(center,center);

            
            .wrap{
                @include flex(center,center); flex-direction:column;
                gap:10px;
            
                .in1{
                    background:#fff; border:3px solid #F97173;
                }
                .in2{
                    background:#F97173; color:#fff; font-weight:bold;
                    font-size:17px; cursor:pointer;
                }
                .in2:hover{
                    background:#F97173bf;
                }
                input{
                    @include whb(100%,40px); box-sizing:border-box; outline:none;
                    padding-left:5px;
                }
                .sign{
                    text-align:right; color:#000; cursor:pointer;
                    display:block; height:40px;
                }
            }
        }
    }
}
    @media all and (min-width:767px){
        .login_wrapper{
        @include whb(550px,300px,#F1E8D9);

        .wrap{
            @include whb(300px,180px); @include flex(space-between,center);

            .sign{
                
                margin-left:241px;
            }
        }
    }
}
    @media all and (max-width:767px){
        .login_wrapper{
            @include whb(240px,250px,#F1E8D9);

            .wrap{
                @include whb(220px,180px); 

                .in1{
                    @include whb(200px,40px);
                }
                .in2{
                    @include whb(200px,40px);
                }
                .sign{
                    margin-left:147px;
                }
            }
        }}

