@import 'Mixin';

nav{
    @include whb(100%,92%);

    div{
        box-sizing:border-box;
    }

    .contents{
        margin:0 auto;
        display:flex; flex-direction: column;
    }
}
@media all and (min-width:1023px){
    .contents{
        @include whb(1020px,97%);
    }
    .com1{
        @include whb(100%,90%); border:30px solid #757575;
    }
    
    .com2{
        @include whb(100%,10%); position:relative;
    }
    .bd2{
        @include whb(40px,100%,#757575); position:absolute;
        left:50%; transform:translateX(-50%);
    }
    .bd3{
        @include whb(380px,28px,#757575); position:absolute;
        left:50%; bottom:0%; transform:translateX(-50%); border-radius:50%; 
    }
}
@media all and (min-width:768px) and (max-width:1022px){
    .contents{
        @include whb(760px,97%);
    }
    .com1{
        @include whb(95%,95%); border:20px solid #757575;
        border-radius:15px;
    }
    .com2{
        display:none;
    }
    .bd2{
        display:none;
    }
    .bd3{
        display:none;
    }
}
@media all and (max-width:767px){
    .contents{
        @include whb(320px,100%);
    }
    .com1{
        @include whb(95%,95%); border:20px solid #757575;
        border-radius:15px;
    }
    .com2{
        display:none;
    }
    .bd2{
        display:none;
    }
    .bd3{
        display:none;
    }
}
