@import 'Mixin';

.signup{
    @include whb(100%,100%,#EFCFBA); @include flex(center,center);

    .signup_wrapper{
        @include whb(550px,95%,#F1E8D9); border-radius:30px;

        form{
            @include whb(100%,100%); @include flex(center,center);

            .wrap{
                @include whb(300px,100%); @include flex(center,normal);
                flex-direction:column; gap:3%;

                .c_label{
                    @include whb(100%,80px); position:relative;

                    img{
                        position:absolute; left:0; bottom:0;
                    }

                    input{
                        position:absolute; right:0; bottom:0;
                    }
                }
                
                label{
                    position:relative;
                }
                .button_wrap{
                    @include whb(100%,40px); @include flex(center,center);
                    justify-content:space-between;
                }
                input{
                    @include whb(100%,30px); box-sizing:border-box; outline:none;
                    padding-left:5px;
                }
                .in1{
                    background:#fff; border:3px solid #F97173;
                }
                .in1_2{
                    width:45%; background:#fff; border:3px solid #F97173;
                }
                .in2{
                    background:#F97173; color:#fff; font-weight:bold;
                    font-size:17px; width:45%; cursor:pointer;
                }
                .in2_2{
                    background:#F97173; color:#fff; font-weight:semi-bold;
                    font-size:15px; width:80px; height: 22px; cursor:pointer; border-radius:5px;
                    position:absolute; right:3%; bottom:7%;
                }
                .in3{@include whb(45%,30px,#FA9A84); box-sizing:border-box;
                    color:#fff; font-weight:bold; padding-left:5px;
                    font-size:17px; cursor:pointer; text-align:center; line-height:30px;
                }
                img{
                    @include whb(45%,70%)
                }
                

            }
        }
    }
    @media all and (max-width:767px){
        .signup_wrapper{
            @include whb(240px,450px,#F1E8D9);
            padding:8px;
        }
    }
}