@import 'Mixin';

.Notepad{
    @include whb(100%,100%);
    display:flex;
    flex-direction:column;
    position:absolute; z-index:999;
    
    .rbd{
        @include whb(100%,2.6%,#F97173);
    }
    .note_wrap{
        @include whb(100%,94.8%,#F1E8D9);
        @include flex(center,center); flex-direction:column; position:relative;

        .delete{
            @include whb(70%,65%,#EFCFBA); position:absolute; border:5px solid #F97173;
            top:50px;

            p{
                text-align:center;
            }
            .delete_t{
                font-size:30px; margin-top:80px;
            }
            .delete_p{
                color:red; margin-top:5px;
            }
            .delete_btn{
                @include whb(200px,50px); margin:60px auto; @include flex(space-between,center);

                button{
                    @include whb(80px,30px,#F97173); color:#fff;
                }
            }
        }

        .fd{
            @include whb(209px,42px,url("./images/flower.png"));
            position:absolute;
        }
        .uf{
            top:4%; left:10px;
        }
        .df{
            bottom:4%; right:10px;
        }
        .note_contents{
            @include whb(85%,500px); border-bottom:3px solid #F97173; position:relative;

            h2{
                border-bottom:2px solid #F97173; font-size:30px; margin-top:30px;
                @include whb(100%,auto)
            }
            .content{
                padding:0 10px; font-size:20px; @include whb(100%,380px); margin-top:20px; overflow-wrap: break-word;  max-height: 400px; overflow-y: auto; 
                display:block; box-sizing:border-box; white-Space: 'pre-wrap';

                p{
                    white-space: pre-wrap;
                }
            }
        }
        .function_area{
            @include whb(85%,80px); margin-top:10px;
            position:relative;

            .area_first{
                float:right; @include flex(center,center); gap:30px;
                a{
                    display:block; @include whb(90px,28px); border:1px solid #F97173; color:#F97173;
                    border-radius:10px; text-align:center;
                }
                a:hover{
                    background:#f0f8ffbd;
                }
                .deleteButton{
                    display:block; @include whb(90px,28px); border:1px solid #F97173; color:#F97173;
                    border-radius:10px; text-align:center; cursor:pointer;
                }
                .deleteButton:hover{
                    background:#f0f8ffbd;
                }
            }
                .area_second{
                    @include whb(100%,40px);
                    @include flex(center,center);
                    .closeButton{
                        @include whb(150px,40px); border:1px solid #F97173; color:#F97173;
                        border-radius:10px; cursor:pointer;
                    }
                    .closeButton:hover{
                        background:#f0f8ffbd;
                    }
                }
                
            
        }
    }
}