@import 'Mixin';

.todolist{
    position:absolute; right:0; @include whb(auto,100%); z-index:99;
    @include flex(center, center);

    .onoff_btn{
        @include whb(25px,145px,#FA9A84); border-radius:15px 0 0 15px;
        @include flex(center, center); cursor:pointer;

        img{
            transition: transform 0.05s ease;
        }
    }
    .onoff_btn.show img {
            transform: rotate(180deg);
        }
    
    .todolist_wrapper{
        @include whb(0,100%,#FA9A84); @include flex(center, center); border-radius:15px 0 0 15px;
        overflow: hidden; 
        transition: width 0.5s ease;
        .content{
            @include whb(500px,600px);

            h2{
                text-align:center; @include whb(100%,60px,#DE8287);
                font-size:32px; color:#fff; line-height:60px;
            }
            article{
                @include whb(100%,540px,#F1E8D9);

                .ul_wrap{
                    @include whb(100%,460px,#61594b); @include flex(center, center);

                    ul{
                        @include whb(90%,350px,#cfc2ac);
                    }
                }
                .input_wrap{
                    @include whb(100%,80px,#c0892a); 
                    
                    form{
                        @include whb(100%,100%); @include flex(center, center);

                        .input_box{
                            @include whb(90%,50px,#cfc2ac);
                        }
                    }
                }
            }
        }
    }
    .todolist_wrapper.show {
        width: 675px;
    }
}
