@import 'Mixin';

.note{
    @include whb(100%,100%);
    display:flex;
    flex-direction:column;
    position:relative;
    
    .rbd{
        @include whb(100%,2.6%,#F97173);
    }
    .note_wrap{
        @include whb(100%,94.8%,#F1E8D9);
        @include flex(center,center);

        .fd{
            @include whb(195px,42px,url("./images/flower.png"));
            position:absolute; z-index:-1;
        }
        .uf{
            top:4%; left:10px;
        }
        .df{
            bottom:4%; right:10px;
        }
        .note_contents{
            @include whb(85%,500px); border:1px solid black;

            .title{
                border-bottom:3px solid #F97173; font-size:30px;
            }
        }
    }
}